*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%) {
          clip-path: inset(100%); }
  clip: rect(0 0 0 0);
  overflow: hidden; }

.site-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.page {
  padding: 0;
  margin: 0;
  background-color: #E5E5E5; }
